const PROD_TERMINAL_CODE = "YHLPC";
// http-server

//
const ROLE_TYPE ={
    SYSTEM:{key:"SYSTEM",value:1,home:"/system/home"},//
    PLATFORM:{key:"PLATFORM",value:10,home:"/platform/home"},//
    TENANT:{key:"TENANT",value:20,home:"/tenant/home"},//
    ORG:{key:"ORG",value:30,home:"/org/home"},//
    ORG_TYPE:{key:"ORG_TYPE",value:40,home:"/org-group/home"},//
    USER:{key:"USER",value:50,home:"/org/home"},//
    compareRole(roleA,roleB){  //01 A>B , -1 A<B
        roleA = ROLE_TYPE.getRoleObject(roleA);
        roleB = ROLE_TYPE.getRoleObject(roleB);
        if(roleA.value==roleB.value){
            return 0;
        }else if(roleA.value>roleB.value){
            return 1;
        }else{
            return -1;
        }
    },
    getRoleObject(roleA){
        if(typeof roleA == "string"){
            for(let r in ROLE_TYPE){
                if(r == roleA){
                    return ROLE_TYPE[r];
                }
            }
            return ROLE_TYPE.USER;
        }else{
            return roleA;
        }
    }
};

export {
    baseUrl,ROLE_TYPE

}
